.App {
  text-align: center;
  color: rgb(20, 20, 20);
}




.SkillDiv{
  margin:0;
  padding: 0;
}

.dateOfBirth {
  margin-bottom: 0;
  color: white;
}

.Intro{
  background-image: linear-gradient(to right top, #204530, #073c38, #05323a, #122734, #191d28, #181b24, #17181f, #16161b, #16191e, #171c20, #191f22, #1b2223);
  padding-bottom: 16px;
  height: 1270px;
}

#Name{
  font-size: 48px;
  color: rgb(255, 255, 255);
}

.Profession{
  color:snow;
}

marquee {
  font-weight: bold;
  color: rgb(247, 15, 15);
  padding: 15px 0px 5px 0px;
}

.About {
  padding: 0px 450px 0px 450px;
  color: whitesmoke;
  font-size: 24px;
}


.ProfilePhoto {
  clip-path: inset(100% 100% 100% 100%);
  /*
    clip-path: circle(50%);
    */
  border: 8px ridge rgb(255, 255, 255);
  margin-top: 40px;
}

