.FollowMe {
  color: white;
  font-size: 32px;
}

.FollowMe > a > button {
  padding: 10px;
  width: 200px;
  font-size: 24px;
  color: rgb(12, 12, 12); 
}


.FollowMe > a > button:hover {
  width: 200px;
  font-size: 24px;
  color: rgb(255, 255, 255);
  background-color: rgb(9, 9, 9);
  border: 2px solid white;
  cursor: pointer;
}
